import anime from "animejs";

//\\//\\//\\//\\ TEXT ANIMATION //\\//\\//\\//\\

export const textAnimation = anime.timeline({
  easing: "easeOutExpo",
  duration: 750,
  autoplay: true
  // loop: true
});

textAnimation
  .add({
    targets: ".fede",
    opacity: [0, 1],
    scaleY: [0.5, 1],
    translateX: ["15em", 0],
    easing: "easeInOutQuint",
    duration: 800
  })
  .add({
    targets: ".plus",
    opacity: [0, 1],
    scaleY: [0.5, 1],
    easing: "easeOutExpo",
    // duration: 600,
    offset: "-=600"
  })
  .add({
    targets: ".ele",
    opacity: [0, 1],
    scaleY: [0.5, 1],
    easing: "easeOutExpo",
    // duration: 600,
    offset: "-=600"
  })
  .add({
    targets: ".date",
    opacity: [0, 1],
    scaleY: [0.5, 1],
    easing: "easeOutExpo",
    duration: 600,
    offset: "-=600"
  });
textAnimation.pause();

//\\//\\//\\//\\ END TEXT ANIMATION //\\//\\//\\//\\
