import chart from "chart.js";
import ChartDataLabels from "chartjs-plugin-datalabels";
chart.plugins.unregister(ChartDataLabels);

export default function(inputLabel) {
  //  // CHART AREA \\ \\

  // YEARS CHART
  const globalLabels = ["2008", "", " ", "", "2019"];
  const barOptions_stacked = {
    plugins: {
      datalabels: {
        align: "center",
        anchor: "center",
        color: "#FFFF",
        font: {
          family: "'Josefin Sans', sans-serif",
          size: 28
        },
        formatter: function(value, context) {
          return globalLabels[context.datasetIndex];
        }
      }
    },
    tooltips: {
      enabled: true
    },
    hover: {
      animationDuration: 0
    },
    scales: {
      xAxes: [
        {
          ticks: {
            beginAtZero: true,
            fontFamily: "'Josefin Sans', sans-serif",
            fontSize: 18
          },
          scaleLabel: {
            display: false
          },
          gridLines: {
            display: false
          },
          stacked: true
        }
      ],
      yAxes: [
        {
          gridLines: {
            display: false,
            color: "#fff",
            zeroLineColor: "#fff",
            zeroLineWidth: 0
          },
          ticks: {
            fontFamily: "'Josefin Sans', sans-serif",
            fontSize: 18
          },
          stacked: true,
          display: false
        }
      ]
    },
    maintainAspectRatio: false,
    legend: {
      position: "top"
    },
    title: {
      display: true,
      text: inputLabel.years.title
    },
    animation: {
      animateScale: true,
      animateRotate: true
    },

    pointLabelFontFamily: "'Josefin Sans', sans-serif",
    scaleFontFamily: "'Josefin Sans', sans-serif"
  };

  const yearsCanvas = document.getElementById("years");

  const yearsChart = new chart(yearsCanvas, {
    type: "horizontalBar",
    data: {
      labels: [""],

      datasets: [
        {
          label:inputLabel.years.dataset[0],
          data: [5],
          backgroundColor: "#D62835"
          // hoverBackgroundColor: "rgba(50,90,100,1)"
        },
        {
          label:inputLabel.years.dataset[1],
          data: [0.5],
          backgroundColor: "#BA212D"
          // hoverBackgroundColor: "rgba(140,85,100,1)"
        },
        {
          label: inputLabel.years.dataset[2],
          data: [2],
          backgroundColor: "#9E1A25"
          // hoverBackgroundColor: "rgba(46,185,235,1)"
        },
        {
          label: inputLabel.years.dataset[3],
          data: [0.5],
          backgroundColor: "#84131E"
          // hoverBackgroundColor: "rgba(46,185,235,1)"
        },
        {
          label: inputLabel.years.dataset[4],
          data: [2],
          backgroundColor: "#6A0D17"
          // hoverBackgroundColor: "rgba(46,185,235,1)"
        }
      ]
    },
    options: barOptions_stacked,
    plugins: [ChartDataLabels]
  });

  // CITY CHART

  const cityCanvas = document.getElementById("city").getContext("2d");

  const configCity = {
    type: "doughnut",
    data: {
      datasets: [
        {
          data: [30, 1, 2, 2],
          backgroundColor: ["#D62835", "#BA212D", "#9E1A25", "#84131E"],
          label: "City"
        }
      ],
      labels: inputLabel.city.dataset
    },
    options: {
      maintainAspectRatio: false,
      legend: {
        position: "top"
      },
      title: {
        display: true,
        text: inputLabel.city.title
      },
      animation: {
        animateScale: true,
        animateRotate: true
      }
    }
  };

  const cityChart = new Chart(cityCanvas, configCity);

  // TRAVEL CHART

  const travelCanvas = document.getElementById("travel");

  const configTravel = {
    type: "bar",
    data: {
      labels: ["2013", "2014", "2015", "2016", "2017", "2018", "2019"],
      datasets: [
        {
          label: inputLabel.travel.title,
          backgroundColor: "#D62835",
          data: [2, 10, 11, 14, 11, 10, 3]
        }
      ]
    },
    options: {
      maintainAspectRatio: false,
      legend: {
        display: false
      },
      title: {
        display: true,
        text: inputLabel.travel.title
      },
      animation: {
        animateScale: true,
        animateRotate: true
      },
      scales: {
        xAxes: [
          {
            ticks: {
              beginAtZero: false,
              fontFamily: "'Josefin Sans', sans-serif",
              fontSize: 18
            },
            scaleLabel: {
              display: false
            },
            gridLines: {}
          }
        ],
        yAxes: [
          {
            gridLines: {
              display: false,
              color: "#fff",
              zeroLineColor: "#fff",
              zeroLineWidth: 0
            },
            ticks: {
              fontFamily: "'Josefin Sans', sans-serif",
              fontSize: 20
            }
          }
        ]
      }
    }
  };

  const travelChart = new Chart(travelCanvas, configTravel);
}
