import anime from "animejs";

export const scoreAnimation = value => {
  const animation = anime.timeline({
    easing: "easeOutExpo",
    duration: 750,
    autoplay: true
  });
  animation
    .add({
      targets: "#finalScore",
      value: [0, value],
      round: 1,
      easing: "easeInOutExpo"
    })
    .add({
      targets: "#finalScore",
      value: [value, 0],
      delay: "2000",
      round: 1,
      easing: "easeInOutExpo"
    });
};


