import anime from "animejs";

//\\//\\//\\//\\ IMAGE ANIMATION //\\//\\//\\//\\

export const imageAnimation = anime.timeline({
  easing: "easeOutExpo",
  duration: 750,
  autoplay: true
  // loop: true
});

imageAnimation
  .add({
    targets: "#fede",
    opacity: [0, 1],
    // scaleY: [0.5, 1],
    translateX: ["-15em", 0],
    easing: "spring(1, 80, 12 0)",
    duration: 800
  })
  .add({
    targets: "#ele",
    opacity: [0, 1],
    // scaleY: [0.5, 1],
    translateX: ["15em", 0],
    // easing: "easeInOutQuint",
    easing: "spring(1, 80, 12 0)",
    duration: 800
  });
imageAnimation.pause();

//\\//\\//\\//\\ END IMAGE ANIMATION //\\//\\//\\//\\
