let score = 0;

const risposte = [
  "prima risposta",
  "seconda risposta",
  "terza risposta",
  "quarta risposta"
];
const nomeForm = [
  "primaDomanda",
  "secondaDomanda",
  "terzaDomanda",
  "quartaDomanda"
];

const myFullpage = new fullpage("#fullpage", {
  slideSelector: ".intro",
  anchors: [
    "facce_slide",
    "invitation_slide",
    "visualization_slide",
    "instruction_slide",
    "consigli_slide",
    "game_slide",
    "conferma_slide"
  ],
  menu: "#floatingMenu",
  loopHorizontal: false,
  afterLoad: function(origin, destination, direction) {
    var params = {
      origin: origin,
      destination: destination,
      direction: direction
    };
    switch (destination.anchor) {
      case "facce_slide":
        imageAnimation.restart();
        break;
      case "invitation_slide":
        textAnimation.restart();
        break;
    }
  },
  afterSlideLoad: (section, origin, destination, direction) => {
    const slidePosition = destination.index;

    if (slidePosition === 0) {
      score = 0;
      Array.from(
        document.querySelectorAll("input:checked"),
        input => (input.checked = false)
      );
    }
    if (score >= 4) {
      score = 4;
    }
    if (score < -4) {
      score = -4;
    }
    if (slidePosition === 4) {
      // Display the final score
      scoreAnimation(score);
      // Reset the checkboxs
      Array.from(
        document.querySelectorAll("input:checked"),
        input => (input.checked = false)
      );
    }

    // console.log("score after slide load:");

    // console.log(score);
  },
  onSlideLeave: function(origin, destination, direction) {
    const slidePosition = destination.index;

    if (slidePosition < 4) {
      score += punteggio(
        choose(nomeForm[slidePosition]),
        risposte[slidePosition]
      );
    }
  }
});

const choose = formToPickUp => {
  // Pick up the form of the acutal slide
  const slideForm = document.getElementById(formToPickUp);
  // Create it as a form
  const data = new FormData(slideForm);
  // Transform it to array to check the length
  const dataToArray = Array.from(data);
  // console.log(dataToArray.length);
  let output = "";

  if (dataToArray === 0) {
    output = "vuoto";
  } else {
    for (const entry of data) {
      output = entry[1];
    }
  }

  return output;
};

const punteggio = (userInput, risposta) => {
  return userInput === risposta ? 1 : -1;
};
